import { useState } from 'react';
import { useCanvas } from './useCanvas';
import warzone from './map-overview-bg.jpg';
import './App.css';

function App() {
  const [ menuRef, coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight ] = useCanvas();

  const handleCanvasClick=(event)=>{
    console.log(event)
    if (event.type === 'click') {
      console.log('Left click');
    } else if (event.type === 'contextmenu') {
      console.log('Right click');
    }
    const currentCoord = { x: event.clientX, y: event.clientY };
    setCoordinates([...coordinates, currentCoord]);
  };

  const doAddContract = (event) =>{
    const currentCoord = { x: event.clientX, y: event.clientY, contract: true, recon: false };
    setCoordinates([...coordinates, currentCoord]);
  }
  const doAddRecon = (event) =>{
    const currentCoord = { x: event.clientX, y: event.clientY, contract: false, recon: true };
    setCoordinates([...coordinates, currentCoord]);
  }
  const doRemove = (event) =>{
    
  }

  const handleClearCanvas=(event)=>{
    setCoordinates([]);
  };

  return (
    <main id="container" className="App-main" >
      <img src={warzone} className="img-warzone" alt="logo" />
  
      <canvas 
        className="App-canvas"
        ref={canvasRef}
        width={canvasWidth}
        height={canvasHeight}
        onClick={handleCanvasClick}
        onContextMenu={handleCanvasClick} />

      <div ref={menuRef} id="contextMenu">
          <div className="contextMenu--option" onClick={doAddContract}>Start Contract</div>
          <div className="contextMenu--option" onClick={doAddRecon}>Add Recon</div>
          <div className="contextMenu--option" onClick={doRemove}>Remove</div>
      </div>
       
      
      <div className="clearButton" >
        <button onClick={handleClearCanvas} > Clear Map </button>
      </div>
    </main>
  );

};

export default App;